@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.MyNavbar {
  display: block; }

.navbar {
  box-shadow: inset 4px 4px 15px rgba(255, 255, 255, 0.1), inset -4px -4px 15px rgba(255, 255, 255, 0.1); }

.CounterRow {
  display: flex;
  width: 100%;
  flex-direction: row; }

.countersRow {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding-top: 5px;
  padding-bottom: 5px;
  box-shadow: inset 4px 4px 15px rgba(255, 255, 255, 0.1), inset -4px -4px 15px rgba(255, 255, 255, 0.1); }

.counterCard {
  box-shadow: none; }

.teamName {
  font-weight: 400;
  font-size: .8rem; }

.toonImg {
  width: 40px;
  height: 40px;
  border: 2px solid #9B47E4;
  border-radius: 50%;
  margin-right: 5px; }

@media only screen and (min-width: 768px) {
  /* for desktop */
  .toonImg {
    width: 45px;
    height: 45px;
    border: 2px solid #9B47E4;
    border-radius: 50%;
    margin-right: 5px; } }

@media only screen and (min-width: 992px) {
  /* for desktop */
  .toonImg {
    width: 55px;
    height: 55px;
    border: 2px solid #9B47E4;
    border-radius: 50%;
    margin-right: 5px; } }

@media only screen and (min-width: 1200px) {
  /* for desktop */
  .toonImg {
    width: 65px;
    height: 65px;
    border: 2px solid #9B47E4;
    border-radius: 50%;
    margin-right: 5px; } }

.Counters5v5 {
  display: flex;
  flex-direction: column;
  text-align: center; }

.columnTitles {
  display: flex;
  flex-direction: row; }

.columnTeams {
  display: flex;
  flex-direction: column; }

.App {
  display: block;
  background-color: black;
  box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.1), inset 0px 0px -10px rgba(255, 255, 255, 0.1);
  font-family: 'Oswald', sans-serif;
  color: whitesmoke; }

